import React, { forwardRef } from "react";

import { Icon } from "../Icon/Icon";
import { colors } from "../styleguide";
import Typography from "../Typography/Typography";

import { IconWrapper, StyledSingleBreadCrumb } from "./Breadcrumbs.styles";
import { ExtendedSingleBreadCrumbProps } from "./Breadcrumbs.types";

const SingleBreadcrumb = forwardRef<HTMLDivElement, ExtendedSingleBreadCrumbProps>(({
	dataQA,
	ariaLabel,
	className,
	mt,
	mb,
	ml,
	mr,
	m,
	$isActive,
	breadCrumbText,
	breadcrumbIndex,
	breadCrumbsLength,
	expandBreadcrumbs = false,
}, ref) => {
	// For breadcrumbs, we only want to show the first, second to last and last breadcrumbs if expandBreadcrumbs prop is false.
	// The rest will be truncated into "..."
	const lastBreadcrumbIndex = breadCrumbsLength - 1;
	const isLast = lastBreadcrumbIndex === breadcrumbIndex;
	// We only want to render content of these indexes if expandBreadcrumbs prop is false
	const renderedIndexes = !expandBreadcrumbs ?
		isLast || breadcrumbIndex === 0 || breadcrumbIndex === lastBreadcrumbIndex - 1
		: breadCrumbsLength;
	// For breadcrumbs truncated into "...", we are, for now, removing any action. This will be handled at a later date: https://wunderkindco.atlassian.net/browse/STORIES-81
	breadCrumbText = renderedIndexes ? breadCrumbText : "...";
	const renderBreadCrumb = renderedIndexes || breadcrumbIndex === 1;

	const BreadCrumb = () => (
		<>
			<StyledSingleBreadCrumb
				ref={ref}
				data-qa={dataQA}
				aria-label={ariaLabel}
				className={className}
				mt={mt}
				mb={mb}
				mr={mr}
				ml={ml}
				m={m}
				aria-current={isLast && "page"}
			>
				<Typography
					dataQA="badge-text"
					variant="bodyCopySmall"
					component="span"
					color={$isActive ? colors.black : colors.midBlue}
				>
					{breadCrumbText}
				</Typography>
			</StyledSingleBreadCrumb>
			{
				!isLast && (
					<IconWrapper>
						<Icon name="ChevronRight" size={16} stroke={`${colors.black}A8`} strokeWidth="2.5px" />
					</IconWrapper>
				)
			}
		</>
	);

	return (
		renderBreadCrumb
			? (
				<BreadCrumb />
			) : null
	);
});

SingleBreadcrumb.displayName = "SingleBreadcrumb";
export default SingleBreadcrumb;

